  .scene {
    width: 50vmin;
    height: 50vmin;
    margin: 2% auto;
    perspective: 175vmin;
    -webkit-box-reflect: below -25vmin linear-gradient(to bottom, #0000 25%, #000);
    
  }
  
  .wrapper {
    width: 100%;
    aspect-ratio: 1;
    transform-style: preserve-3d;
    z-index: -2;
  }
  
  .sphere {
    position: relative;
    width: 100%;
    height: 100%;
    margin: 0 auto;
    transform-style: preserve-3d;
    animation: rotate 7.5s infinite linear;
    top: -8%;
    left: 10%;
   
  }
  .sphere .ring {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    aspect-ratio: 1;
    border-radius: 50%;
    border: 6px dashed;
    
  }
  
  .sphere .ring:nth-child(1) {
    color: #c0c0c0; /* Light Grey */
    transform: rotateY(4.5deg);
  }
