@import url("https://fonts.googleapis.com/css2?family=Titillium+Web:wght@300;400;700&display=swap");

* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: "Titillium Web", sans-serif;
}

:root {
    --mainColor: #29335c44;
    --mainColorLight: #5768aa46;
    --secondaryColor: #db2b39;
    --textColor: #eee;
}

body {
    margin: 0;
    padding: 0;
    background-image: url('../public/images/biii.jpg'); /* Replace 'your-background-image-url.jpg' with your actual image URL */
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    height: 100vh;
    
}

header {
    position: relative; /* Set header to relative position */
    z-index: 1; /* Ensure header is above background */
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 80px;
    padding: 0 2rem;
    background-color: rgba(8, 13, 30, 0.231); /* Add transparency to the header background */
    color: var(--textColor);
}

nav {
    position: absolute; /* Position the nav absolutely */
    right: 2rem; /* Adjust as needed */
    top: 50%; /* Align the nav vertically to the middle */
    transform: translateY(-50%); /* Adjust as needed */
}

nav a {
    margin-left: 1rem; /* Spacing between links */
    color: var(--textColor);
    text-decoration: none;
}

nav a:hover {
    color: var(--secondaryColor);
}

header .nav-btn {
    padding: 5px;
    cursor: pointer;
    background: transparent;
    border: none;
    outline: none;
    color: var(--textColor);
    visibility: hidden;
    opacity: 0;
    font-size: 1.8rem;
}

header div,
nav {
    display: flex;
    align-items: center;
}

@media only screen and (max-width: 1024px) {
    header .nav-btn {
        visibility: visible;
        opacity: 1;
    }

    header nav {
        position: fixed;
        top: -100vh;
        left: 0;
        height: 100%;
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        gap: 1.5rem;
        background-color: var(--mainColor);
        transition: 1s;
    }

    header .responsive_nav {
        transform: translateY(100vh);
    }

    nav .nav-close-btn {
        position: absolute;
        top: 2rem;
        right: 2rem;
    }

    nav a {
        font-size: 1.5rem;
    }
}
